'use client'

import React, { useState } from 'react'

export default function NewsletterForm() {
  const [email, setEmail] = useState('')
  const [error, setError] = useState('')
  const [success, setSuccess] = useState(false)

  const validateEmail = (email: string) => {
    return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    if (!validateEmail(email)) {
      setError('Please enter a valid email address.')
      setSuccess(false)
      return
    }
    setError('')
    setSuccess(true)
    setEmail('')
    // Simulate API call or subscription logic here
  }

  return (
    <div className="max-w-3xl mx-auto bg-gradient-to-r from-purple-50 to-white rounded-lg p-8 relative">
      <h2 className="text-2xl font-semibold mb-4 text-center">Subscribe to get information, latest news and other interesting offers about Jadoo</h2>
      <form onSubmit={handleSubmit} className="flex flex-col sm:flex-row gap-4 max-w-xl mx-auto">
        <label htmlFor="email" className="sr-only">
          Email address
        </label>
        <input
          id="email"
          type="email"
          placeholder="Your email"
          className="flex-grow px-4 py-3 rounded-md border border-gray-300 focus:outline-none focus:ring-2 focus:ring-orange-500"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          aria-invalid={!!error}
          aria-describedby="email-error"
          required
        />
        <button
          type="submit"
          className="px-6 py-3 bg-orange-500 text-white rounded-md hover:bg-orange-600 transition"
        >
          Subscribe
        </button>
      </form>
      {error && (
        <p id="email-error" className="mt-2 text-sm text-red-600 text-center">
          {error}
        </p>
      )}
      {success && (
        <p className="mt-2 text-sm text-green-600 text-center">
          Thank you for subscribing!
        </p>
      )}
      {/* Decorative floating icon can be added here if needed */}
    </div>
  )
}
