'use client'

import React from 'react'
import clsx from 'clsx'

type ButtonProps = React.ButtonHTMLAttributes<HTMLButtonElement> & {
  variant?: 'primary' | 'outline'
  children: React.ReactNode
}

export default function Button({ variant = 'primary', children, className, ...props }: ButtonProps) {
  const baseStyles =
    'inline-flex items-center justify-center rounded-md px-5 py-3 text-sm font-semibold transition-colors focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-orange-500 disabled:opacity-50 disabled:pointer-events-none'

  const variantStyles = {
    primary: 'bg-orange-500 text-white hover:bg-orange-600',
    outline: 'border border-orange-500 text-orange-500 hover:bg-orange-50',
  }

  return (
    <button className={clsx(baseStyles, variantStyles[variant], className)} {...props}>
      {children}
    </button>
  )
}
